      312: {"desc": "  ", "day": "wi-day-hail", "night": "wi-night-alt-hail",},
      313: {"desc": "   ", "day": "wi-day-hail", "night": "wi-night-alt-hail",},
      314: {"desc": "   ", "day": "wi-day-hail", "night": "wi-night-alt-hail",},
      321: {"desc": "", "day": "wi-day-rain", "night": "wi-night-alt-rain",},
      500: {"desc": " ", "day": "wi-day-rain", "night": "wi-night-alt-rain",},
      501: {"desc": " ", "day": "wi-day-rain", "night": "wi-night-alt-rain",},
      502: {"desc": " ", "day": "wi-day-rain", "night": "wi-night-alt-rain",},
      503: {"desc": "  ", "day": "wi-day-rain", "night": "wi-night-alt-rain",},
      504: {"desc": " ", "day": "wi-day-rain", "night": "wi-night-alt-rain",},
      511: {"desc": " ", "day": "wi-day-rain", "night": "wi-night-alt-rain",},
      520: {"desc": "  ", "day": "wi-day-rain", "night": "wi-night-alt-rain",},
      521: {"desc": "", "day": "wi-day-rain", "night": "wi-night-alt-rain",},
      522: {"desc": "  ", "day": "wi-day-rain", "night": "wi-night-alt-rain",},
      531: {"desc": "  ", "day": "wi-day-rain", "night": "wi-night-alt-rain",},
      600: {"desc": " ", "day": "wi-day-snow", "night": "wi-night-alt-snow",},
      601: {"desc": "", "day": "wi-day-snow", "night": "wi-night-alt-snow",},
      602: {"desc": " ", "day": "wi-day-snow", "night": "wi-night-alt-snow",},
      611: {"desc": " ", "day": "wi-day-rain-mix", "night": "wi-night-alt-rain-mix",},
      612: {"desc": "  ", "day": "wi-day-rain-mix", "night": "wi-night-alt-rain-mix",},
      613: {"desc": "   ", "day": "wi-day-rain-mix", "night": "wi-night-alt-rain-mix",},
      615: {"desc": "   ", "day": "wi-day-rain-mix", "night": "wi-night-alt-rain-mix",},
      616: {"desc": "  ", "day": "wi-day-rain-mix", "night": "wi-night-alt-rain-mix",},
      620: {"desc": " ", "day": "wi-day-snow", "night": "wi-night-alt-snow",},
      621: {"desc": "", "day": "wi-day-snow", "night": "wi-night-alt-snow",},
      622: {"desc": " ", "day": "wi-day-snow", "night": "wi-night-alt-snow",},
      701: {"desc": "", "day": "wi-day-fog", "night": "wi-night-fog",},
      711: {"desc": "", "day": "wi-smoke", "night": "wi-smoke",},
