/**   */

const toastContainer = document.querySelector('.toasts-container');

/**
 *       
 * @param allInputs -  
 * @param mode - : "selected" - , "all" - 
 * @returns {*[]} -   value 
 */
function selectedCheckboxes(allInputs, mode) {
  switch (mode) {
    case "selected":
      return Array.from(allInputs).filter(input => input.checked).map(c => c.value.toLowerCase());
    case "all":
      return Array.from(allInputs).map(c => c.value.toLowerCase());
  }
}

/**
 *    ,   ,  
 * @param toast - HTML-     ,     ,  
 * @param container -   
 * @param toastClass -  ,     ,     
 * @param toastParams -   
 */
function showBsToast(toast, container, toastClass, toastParams) {
  /**    */
  const hiddenToasts = container.querySelectorAll('.hide');
  if (hiddenToasts) {
    hiddenToasts.forEach((hiddenToast) => {
      container.removeChild(hiddenToast);
    });
  }
  container.insertAdjacentHTML('beforeend', toast);
  const toastElList = [].slice.call(document.querySelectorAll('.toast' + toastClass));
  toastElList.map(function (toastEl) {
    return new bootstrap.Toast(toastEl, toastParams).show()
  });
}

/**
 * 
 */
class Toast {
  /**
   * @param header -   
   * @param text - 
   * @param time -    
   * @param type - : toast - , errorToast - ,   , miniToast - , ,  
   * @param color - 
   */
  constructor(header, text, time, type, color) {
    this.header = header;
    this.text = text;
    this.time = time;
    this.color = color;
    this.toastParam = {autohide: true};
    switch (type) {
      case "toast":
        this.class = "toast-info"
        this.toastElement = '<div class="toast ' + this.class + ' fade" role="alert" aria-live="assertive" aria-atomic="true"><div class="toast-header"><i class="mdi mdi-message-alert-outline"></i><strong class="me-auto">' + this.header + '</strong><small class="text-muted">' + this.time + '</small><button type="button" class="btn-close" data-bs-dismiss="toast" aria-label=""></button></div><div class="toast-body">' + this.text + '</div></div>';
        break;
      case "errorToast":
        this.class = "toast-error"
        this.toastElement = '<div class="toast ' + this.class + ' fade" role="alert" aria-live="assertive" aria-atomic="true"><div class="toast-header"><i class="mdi mdi-alert text-danger"></i><strong class="me-auto text-danger">' + this.header + '</strong><small class="text-muted">' + this.time + '</small><button type="button" class="btn-close" data-bs-dismiss="toast" aria-label=""></button></div><div class="toast-body">' + this.text + '</div></div>';
        this.toastParam = {autohide: false};
        break;
      case "miniToast":
        this.class = "toast-info"
        this.toastElement = '<div class="toast ' + this.class + ' align-items-center bg-' + this.color + '-lighter" role="alert" aria-live="assertive" aria-atomic="true"><div class="d-flex"><div class="toast-body">' + this.text + '</div><button type="button" class="btn-close me-2 m-auto" data-bs-dismiss="toast" aria-label=""></button></div></div>';
        break;
    }
  }

  /**
   *  
   */
  show() {
    showBsToast(this.toastElement, toastContainer, "." + this.class, this.toastParam)
  }
}

/**
 *  Cookie
 * @param name
 * @returns {string|null}
 */
function getCookie(name) {
  const nameEQ = name + "=";
  const ca = document.cookie.split(';');
  for (let i = 0; i < ca.length; i++) {
    let c = ca[i];
    while (c.charAt(0) === ' ') c = c.substring(1, c.length);
    if (c.indexOf(nameEQ) === 0) return c.substring(nameEQ.length, c.length);
  }
  return null;
}

/**
 * ID 
 * @type {string|null}
 */
const cookieID = getCookie('aut[id]');

const jwtKey = getCookie('aut[jwt]');


/**
 * Ajax.  GET  POST 
 * @param method - POST  GET
 * @param url - 
 * @param parameters -  get     POST
 * @param datatype
 * @param callback -     
 */
// FIXME   fetch
const ajax_send = (method, url, parameters, datatype, callback) => {
  let xhr = new XMLHttpRequest();

  switch (method) {
    case "GET":
      let queryString;
      if (parameters !== null) {
        if (typeof parameters === 'object' &&
          !Array.isArray(parameters)) {
          queryString = Object.keys(parameters).map(key => key + '=' + parameters[key]).join('&');
        } else {
          queryString = parameters;
        }
      } else {
        queryString = null;
      }
      xhr.open(method, url + "?" + queryString, true);
      xhr.setRequestHeader('Accept', 'application/json');
      xhr.setRequestHeader('Authorization', `Bearer ${jwtKey}`);
      xhr.send(null);
      break;

    case "POST":
      xhr.open(method, url, true);
      xhr.setRequestHeader('Accept', 'application/json');
      xhr.setRequestHeader('Authorization', `Bearer ${jwtKey}`);
      xhr.send(parameters);
      break;
  }

  xhr.onreadystatechange = () => {
    if (xhr.readyState !== 4) {
      return;
    }

    let header = '';

    if (xhr.status === 200) {
      let result;

      if (method === "GET") {
        if (datatype === "json") {
          result = typeof xhr.response !== "string"
            ? JSON.stringify(xhr.response)
            : xhr.response;
          try {
            JSON.parse(xhr.response);
            result = JSON.parse(xhr.response);
            callback(result);
          } catch (e) {
            new Toast("", '  : ' + e + ' : ' + xhr.responseText, moment().tz('Europe/Moscow').format('YYYY-MM-DD'), "errorToast", "").show();
          }
        } else if (datatype === "text") {
          callback(xhr.responseText);
        }
      }
      if (method === "POST") {
        if (!xhr.response) {
          callback('null');
        } else {
          new Toast("", xhr.responseText, moment().tz('Europe/Moscow').format('YYYY-MM-DD'), "errorToast", "").show();
        }
      }

    } else if (xhr.status === 0) {
      header = " .  ";
      new Toast(header, xhr.responseText, moment().tz('Europe/Moscow').format('YYYY-MM-DD'), "errorToast", "").show();
    } else if (xhr.status === 404) {
      header = "404. Not Found.     ";
      new Toast(header, xhr.responseText, moment().tz('Europe/Moscow').format('YYYY-MM-DD'), "errorToast", "").show();
    } else if (xhr.status === 500) {
      header = "   [500]";
      new Toast(header, xhr.responseText, moment().tz('Europe/Moscow').format('YYYY-MM-DD'), "errorToast", "").show();
    }
  }

}

export {selectedCheckboxes, getCookie, cookieID, ajax_send, Toast, showBsToast, toastContainer}
